body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  text-align: center;
  background: #000;
  color: white;
  margin: 0;
  padding: 10px 0;
  font-size: 1.2rem;
}

.search {
  padding: 10px 0;
  text-align: center;
}

.search input {
  border: 1px solid rgba(223, 223, 223, 0.41);
  padding: 3px 20px;
  outline: none;
  border-radius: 20px 0 0 20px ;
  font-size: .7rem;
}

.search input::placeholder {
  font-size: .6rem;
}

.search button {
  border: none;
  padding: 2px 10px;
  background: rgb(32, 216, 32);
  border-radius: 0 20px 20px 0;
  color: white;
  cursor: pointer;
  outline: none;
}

.search button:hover {
  background: rgb(7, 167, 7);
  color: white;
}

.container {
  width: 80%;
  margin: 50px auto;
}

.recipes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 50px;
}

.recipe {
  width: 250px;
  height: 250px;
  background: white;
  box-shadow: 0px 0px 13px -5px grey;
  padding: 10px;
  border-radius: 3px;
  transition: all .5s;
}

.recipe:hover {
  margin-top: -2px;
  box-shadow: 0px 5px 13px -5px grey;
}

.image {
  width: 250px;
  height: 150px;
  object-fit: cover;
}
p {
  font-size: .8rem;
}

.copyright {
  background: #000;
  color: white;
  text-align: center;
  margin: 0;
  padding: 5px;
}